import Vue from 'vue'
import Router from 'vue-router'
import home from '~/components/home.vue'
import NotFoundComponent from '~/pages/404.vue'

const days = (resolve) => {
    import ('~/components/days.vue').then((module) => {
        resolve(module)
    })
}

const chinas = (resolve) => {
    import ('~/components/chinas.vue').then((module) => {
        resolve(module)
    })
}

const homeContent = (resolve) => {
    import ('~/components/homeContent.vue').then((module) => {
        resolve(module)
    })
}

const products = (resolve) => {
    import ('~/components/products.vue').then((module) => {
        resolve(module)
    })
}

const contents = (resolve) => {
    import ('~/components/contents.vue').then((module) => {
        resolve(module)
    })
}

const operate = (resolve) => {
    import ('~/components/operate.vue').then((module) => {
        resolve(module)
    })
}

const login = (resolve) => {
    import ('~/components/login.vue').then((module) => {
        resolve(module)
    })
}

const we = (resolve) => {
    import ('~/components/we.vue').then((module) => {
        resolve(module)
    })
}

const company = (resolve) => {
    import ('~/components/company.vue').then((module) => {
        resolve(module)
    })
}

const companyRight = (resolve) => {
    import ('~/components/companyRight.vue').then((module) => {
        resolve(module)
    })
}

const join = (resolve) => {
    import ('~/components/join.vue').then((module) => {
        resolve(module)
    })
}

const photo = (resolve) => {
    import('~/components/photo.vue').then((module) => {
        resolve(module)
    })
}


Vue.use(Router)

export default new Router({
    routes: [{
        path: '/',
        redirect: '/home'
    }, {
        path: '/home',
        component: home,
        children: [{
                path: '/home',
                redirect: '/home/homeContent'
            }, {
                path: '/home/homeContent',
                component: homeContent,
                children: [{
                    path: '/home/homeContent',
                    redirect: '/home/homeContent/days'
                }, {
                    path: '/home/homeContent/days',
                    component: days,
                }, {
                    path: '/home/homeContent/chinas',
                    component: chinas,
                }]
            }, {
                path: '/products/:id',
                component: products,
            }, {
                path: '/contents/:id',
                name: 'contents',
                component: contents,
            }, {
                path: '/login',
                name: 'login',
                component: login,
            }, {
                path: '/photo',
                name: 'photo',
                component: photo,
            }, {
                path: '/we',
                name: 'we',
                component: we,
            },
            {
                path: '/join',
                name: 'join',
                component: join,
            },
            {
                path: '/company',
                name: 'company',
                component: company,
                children: [{
                    path: '/company/companyRight',
                    redirect: '/company/companyRight/0'
                }, {
                    path: '/company/companyRight/:id',
                    component: companyRight,
                }]
            }
        ],

    }, {
        path: '*',
        component: NotFoundComponent
    }]
})


