<template>
  <div class="news">
    <div class="news-day news-b animated fadeInLeft delay_0">
      <swiper :options="swiperOption">
          <swiper-slide v-for="item in news[0].children" :key="item.title">
            <router-link :to="{ name: 'contents', params: { id: item.id}}">
              <img :src="item.img" alt="">
            </router-link >
            </swiper-slide>
          <div class="swiper-pagination" slot="pagination"></div>
      </swiper>
    </div>
    <div class="news-cp news-b">
      <div class="news-yy animated fadeInDown delay_0">
        <router-link :to="{ name: 'contents', params: { id: this.news1[0].id}}">
          <img :src="this.news1[0].img" alt="">
        </router-link>
      </div>
      <div class="news-yy animated fadeInUp delay_100">
        <router-link :to="{ name: 'contents', params: { id: this.news1[1].id}}">
          <img :src="this.news1[0].img" alt="">
        </router-link>
      </div>
    </div>
    <div class="news-now news-b  animated fadeInRight delay_0">
      <h3>科技的魅力</h3>
      <router-link :to="{ name: 'contents', params: { id: this.news1[2].id}}">
          <img :src="this.news1[0].img" alt="">
      </router-link>
      <p>线下课程丨全面构建运营知识体系，涨薪前让自己更有实力</p>
      <span>2018-1-20 9:00-17:30</span>
    </div>
  </div>
</template>

<script>
import { news } from "~/data/news.js";

export default {
  data() {
    return {
      swiperOption: {
        autoplay: 4000,
        setWrapperSize: true,
        paginationClickable: true,
        mousewheelControl: true,
        observeParents: true,
        loop: true,
        centeredSlides: true,
        // slidesPerView: "0",
        spaceBetween: 30
      },
      news: news,
      news1: [
        {
          id: "100001",
          img: "http://ooi407n8x.bkt.clouddn.com/anankeji.jpg"
        },
        {
          id: "100002",
          img: "http://ooi407n8x.bkt.clouddn.com/an.jpg"
        },
        {
          id: "100003",
          img: "http://ooi407n8x.bkt.clouddn.com/an.jpg"
        }
      ]
    };
  },
  components: {}
};
</script>

<style scoped lang="scss">
.news {
  margin-bottom: 30px ;
  width: 100%;
  img {
    width: 100%;
  }
}

.news::after {
  content: "\0020";
  display: block;
  height: 0;
  clear: both;
}

.news-b {
  overflow: hidden;
  float: left;
}

.news-day {
  width: 600px;
  overflow: hidden;
  margin-right: 20px;
}

.news-cp {
  width: 260px;
  overflow: hidden;
  margin-right: 20px;
}

.news-yy {
  width: 281.5px;
  overflow: hidden;
  margin-bottom: 20px;
}

.news-yy:nth-child(2) {
  margin-bottom: 0;
}

.news-now {
  height: 333.33px;
  width: 300px;
  background: #334259;
  color: #fff;
  padding: 15px;
  h3 {
    margin-bottom: 10px;
  }
  img {
    margin-bottom: 10px;
  }
  p {
    color: #a6b9d3;
    line-height: 25px;
    margin-bottom: 20px;
  }
  span {
    color: #a6b9d3;
  }
}
</style>



