<template>
  <div class="home">
    <header>
      <ul>
        <li>
          <router-link to="/we">
            <img src="http://ooi407n8x.bkt.clouddn.com/logoan.jpg" alt="">
          </router-link>
        </li>
        <li>
          <router-link to="/home">医药首页</router-link>
        </li>
        <li>
          <router-link to="/products/1">产品制作</router-link>
        </li>
        <li>
          <router-link to="/photo">公司产品</router-link>
        </li>
        <li>
          <router-link to="/join">合作流程</router-link>
        </li>
        <li>
          <router-link to="/company/companyRight/0">公司简介</router-link>
        </li>
        <li class="header-right">
          <router-link to="/login">注册 | 登录</router-link>
        </li>
        <!-- <li class="header-right">
          <router-link to="/an">微信公众平台</router-link>
        </li> -->
      </ul>
    </header>
    <div class="container">
      <router-view :key="key"></router-view>
    </div>
    <footer>
      <div class="footer-box-in">
        <div class="footer-box">
          <div class="footer-title">关于</div>
          <section>
            娜乌西卡是风之谷中的女主角，看多宫崎骏漫画的人应该都知道这一点，在人类都认为森林在释放毒气的时候，只有女主角娜乌西卡了解到森林在净化空气，娜乌西卡最终用自己的爱感化了所有人，我希望娜乌西卡科技有限公司也在医药行业担任这样的角色，我们存在的目的是为了真正的去改变人类的健康，而不是简单的以商人的身份没有梦想的活着，这就是娜乌西卡文化。
          </section>
        </div>
        <div class="footer-box">
          <div class="footer-title">链接</div>
          <ul v-for="item in footers" :key="item.label">
            <li>
              <a :href="item.router" target="_blank">{{ item.title }}</a>
            </li>
          </ul>
        </div>
        <div class="footer-box">
          <div class="footer-title">微信</div>
          <img src="http://ooi407n8x.bkt.clouddn.com/an.jpg" alt="">
        </div>
      </div>
    </footer>
  </div>
</template>

<script>
import news from "~/components/news.vue";
import mainLeft from "~/components/mainLeft.vue";
import mainRight from "~/components/mainRight.vue";
import { footers } from "~/data/index";

export default {
  data() {
    return {
      footers: footers
    };
  },
  components: {
    news,
    mainLeft,
    mainRight
  },
  computed: {
    key() {
      return this.$route.name !== undefined
        ? this.$route.name + +new Date()
        : this.$route + +new Date();
    }
  },
  created() {
    window.scrollTo(0, 0);
  }
};
</script>

<style lang="scss">
.container {
  margin: 0 auto;
  width: 1200px;
  position: relative;
}

header {
  background: #fff;
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
  img{
    width:50px;
  }
  ul {
    padding: 10px 0;
    position: relative;
    margin: 0 auto;
    width:1200px;
    li:nth-child(1) {
      font-size: 16px;
      font-weight: bold;
    }
    li {
      display: inline-block;
      padding-right: 80px;
      a {
        display: block;
        color: #123;
        border-bottom: 3px solid transparent;
        padding-bottom: 3px;
        padding-top: 6px;
        line-height: 60px;
        &:hover {
          color: #e60012;
          font-weight: bold;
        }
      }
      .router-link-active {
        color: #e60012;
        font-weight: bold;
      }
    }
    .header-right {
      float: right;
    }
  }
}

main {
  display: flex;
}

.homeContent-center::after {
  content: "\0020";
  display: block;
  height: 0;
  clear: both;
}

.mainLeft {
  width: 300px;
  margin-right: 20px;
  margin-bottom: 30px;
  float: left;
}

.mainRight {
  overflow: hidden;
}

footer {
  background-color: #000;
  color: #fff;
  padding: 100px 0;
  .footer-box-in {
    width: 1200px;
    margin: 0 auto;
  }
  .footer-box-in::after {
    content: "\0020";
    display: block;
    height: 0;
    clear: both;
  }
  .footer-box:nth-child(2) {
    width: 450px;
  }
  .footer-box:nth-child(3) {
    width: 200px;
    margin-right: 0;
  }
  a {
    color: #fff;
  }
  .footer-box {
    width: 450px;
    float: left;
    margin-right: 30px;
    line-height: 28px;
    section {
    }
    img {
      width: 150px;
    }
    ul {
      li {
        margin-bottom: 7px;
        margin-right: 15px;
        float: left;
      }
    }
  }
}

.footer-title {
  font-weight: 600;
  color: #fff;
  margin-bottom: 20px;
  font-size: 19px;
}
</style>



